import { useEffect, useState } from "react";

const root = document.querySelector(":root");

const LOCAL_STORAGE_KEY = 'stTheme'

const themes = {
  1: {
    navcolor: "#323235",
    maincolor: "#282828",
    secondmaincolor: "#404040",
    navtextcolor: "white",
    titlecolor: "white",
    text: "white",
    invertext: "black",
    color1: "#399E5A",
    color2: "#5ABCB9",
    color3: "#644CAD",
  },
  2: {
    navcolor: "#3c3836",
    maincolor: "#282828",
    secondmaincolor: "#504945",
    navtextcolor: "#ebdbb2",
    titlecolor: "#ebdbb2",
    text: "#ebdbb2",
    invertext: "#282828",
    color1: "#cc241d",
    color2: "#d79921",
    color3: "#98971a",
  },
  3: {
    navcolor: "white",
    maincolor: "#d7daea",
    secondmaincolor: "#e6e7ef",
    navtextcolor: "#grey",
    titlecolor: "#black",
    text: "#black",
    invertext: "#white",
    color1: "#F75C03",
    color2: "#FFC857",
    color3: "#0E79B2",
  }
};

export const useTheme = () => {
    const [theme, setTheme] = useState(localStorage.getItem(LOCAL_STORAGE_KEY) || 1);

    useEffect(() => {
        localStorage.setItem(LOCAL_STORAGE_KEY, theme)
        
        Object.entries(themes[theme] || {}).forEach( ([key, color]) => {
            root.style.setProperty(`--${key}`, color)
        } )
    }, [theme])

    return {
        changeTheme: () => setTheme(theme < 3 ? theme + 1 : 1)
    }
}
