function Article({title, file}) {
  return (
    <li href="" className="articleButton" filename={file}>
      <p>{title}</p>
      <div className="slidebutton">
        <span>
          <i className="fas fa-arrow-right"></i>
        </span>
      </div>
    </li>
  );
}

const articles = [{ title: "why I use vim", file: "whyiusevim.md" }];

function ArticleList() {
  return (
    <div className="articleList">
      <ul>
        {
          //Article("Why do i use vim")
          articles.map(
            (article, index) => <Article title={article.title} file={article.file} key={'article-'+index+articles.length}/>
          )
        }
      </ul>
    </div>
  );
}

export default ArticleList;
